import React, { useRef, useState } from "react";
import classes from "./Container.module.scss";

export const Container = ({children, flexState = {justifyContent: "space-between", alignItems: "center",}, width ="50%",height = "auto", left, top, centred = false, borders = false, ...props}) => {

//Config:
//====================================================
const container = useRef(null);
//----------------------------------------------------
let className = classes.container;
//----------------------------------------------------
if (centred) {
    className += " " + classes.container_centred;
}
//----------------------------------------------------
if (borders) {
    className += " " + classes.container_borders
}
//----------------------------------------------------

//====================================================

//Render:
//----------------------------------------------------
return (
    <div 
    onClick={(e) => console.log(container.current)} 
    className={className} 
    style={{width: width, height: height, top:  top, left: left, ...flexState}} 
    {...props}
    ref={container}
    
    >
    {children}
    </div>
    
)
//----------------------------------------------------
}