import React from "react";
import classes from "./Button.module.scss";

export const Button = ({text, bgColor, handler, ...props}) => {

    //Config:




    //Render:
    return (
        <button 
        className={classes.btn}
        onClick={handler}
        style={{backgroundColor: bgColor}}
        {...props}
        >{text}
        </button>
    )
}