import React from "react";
import classes from "./Modal.module.scss";

export const Modal = (modal, {active, setActive}, ...props) => {
  //     Создать компонент Modal который должен иметь следующие свойства, передаваемые из родительского компонента:

  // Текст заголовка модального окна (свойство header)
  // Должен ли присутствовать крестик для закрытия окна справа вверху (boolean свойство closeButton, значения true/false)
  // Основной текст модального окна, который будет показан в его центральной части (свойство text)
  // Кнопки, которые находятся в нижней части модального окна, переданные в виде кода в формате JSX (свойство actions)
  //Config:

  //Render:
  return (
    <div className={active ? classes.modal__overlay + "active" : classes.modal__overlay}>
      <div className={classes.modal}>
        <div className={classes.modal__header}>{modal.header}
          <button className={classes.modal__header__closeBtn} onClick={(e) => setActive(false)}></button>
        </div>
        <div className={classes.modal__body}>{modal.text}</div>
        <div className={classes.modal__footer}>
          <button className={classes.modal__footer__successBtn}>Ok</button>
          <button onClick={(e) => setActive(false)} className={classes.modal__footer__closeBtn}>Cancel</button>
        </div>
      </div>
    </div>
  );
};
