import React, { useState } from "react";
import { Button } from "./components/Button/Button";
import { Container } from "./components/Container/Container";
import { Modal } from "./components/Modal/Modal";
import { configs } from "./data/componentsConfig";
import "./style.scss";

function App() {
  //Config:
  const modal = {
    header: "Do you want to delete this file?",
    text: `Once you delete this file, it won't be possible to undo this action. Are you sure you want to delete it?`
  }
  //=========================================
  //Btns pressets:
  const [modalActive, setModalActive] = useState(true)
  //=========================================

  //Render:
  //-----------------------------------------

  return (
    <div className="App">
          <Modal active={modalActive} setActive={setModalActive} header= {modal.header} text={modal.text}/>
          
        <Container centred top="65%" borders width="40%">
        <Button handler={(e) => setModalActive(true)} bgColor={configs.btnConfig.bgColor.btn_1} text={configs.btnConfig.text.btn_1}></Button>
        <Button handler={(e) => setModalActive(true)} bgColor={configs.btnConfig.bgColor.btn_2} text={configs.btnConfig.text.btn_2}></Button>
        </Container>
    
    </div>
  );
  //-----------------------------------------

}

export default App;
